<template>
  <!-- slider -->
  <div style="width:100%;height:100%">
    <!-- slider -->
    <slider ref="slider" :options="options">
      <!-- slideritem slot -->
      <slideritem
        v-for="(item, index) in someList"
        :key="index"
        :style="item.style"
        v-html="item.html"
      ></slideritem>
      <!-- loading, -->
      <!-- <div slot="loading">loading...</div> -->
    </slider>
  </div>
</template>
<script>
import { slider, slideritem } from "vue-concise-slider"; // import slider components
export default {
  el: "#app",
  data() {
    return {
      //Image list
      someList: [
        {
          html:
            '<div><p class="step">SDK()<p><img src="/static/images/start1.png"></div>',
          style: {
            background: "#1bbc9b"
          }
        },
        {
          html:
            '<div><p class="step">jsuserusermirror-anonymous<p><img src="/static/images/start2.png"></div>',
          style: {
            background: "#4bbfc3"
          }
        },
        {
          html:
            '<div><p class="step">actioncategory<p><img src="/static/images/start3.png"></div>',
          style: {
            background: "#7baabe"
          }
        }
      ],
      //Sliding configuration [obj]
      options: {
        // direction: 'vertical',
        effect: "nest",
        //autoplay:1000,
        pagination: true,
        thresholdDistance: 100,
        thresholdTime: 300,
        grabCursor: true,
        speed: 300,
        loop: true
      }
    };
  },
  components: {
    slider,
    slideritem
  },
  mounted() {},
  methods: {
    // Listener event
    slide(data) {
      console.log(data);
    },
    onTap(data) {
      console.log(data);
    },
    onInit(data) {
      console.log(data);
    }
  }
};
</script>
<style lang="less" >
.step {
  width: 60%;
  white-space: pre-wrap;
  margin: 0 auto;
  font-size: 20px;
  text-indent: 2em;
  text-align: left;
  font-family: cursive;
}
</style>



